
import $ from 'jquery';
import { MessageBox } from "mint-ui";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

if(window.plus){

document.addEventListener("plusready", function() {

plus.key.addEventListener('backbutton', function() {

if (confirm('')) {
plus.runtime.quit();
}
}, false);
});
}
export default {
directives: {
TransferDom
},
components: { TransferDom, Popup, Group, Cell, XButton, Flexbox, FlexboxItem },
data(){
return {
show: true,
is_face: false,
video: null,
face_img: ''
}
},
created(){

},
mounted(){
this.$refs.mydiv.style.height = window.innerHeight + 'px';
},
methods:{
check(){
this.show = !this.show;
if (this.show) {
$("#password").prop('type','password');
}else{
$("#password").prop('type','text');
}
},
login(){
let userValue = this.$refs.username.value;
userValue = userValue.trim();
let pwdValue = this.$refs.password.value;
pwdValue = pwdValue.trim();
if(userValue.length === 0){
MessageBox('', '');
return false;
};
let pass =  (/^1[3|4|5|7|8][0-9]{9}$/).test(userValue);
if (!pass) {
MessageBox('', '');
return false;
};
if(pwdValue.length === 0){
MessageBox('', '');
return false;
};
this.$router.push('/dui');
},

create_role() {
this.$router.push('/ids/create-role');
},

set_pwd() {
this.$router.push('/ids/set-password');
},

open_face() {
this.is_face = true;
this.initVideo();
},

initVideo() {
var _this = this;
var URL = window.URL || window.webkitURL;   
navigator.getUserMedia = navigator.getUserMedia
|| navigator.webkitGetUserMedia
|| navigator.mozGetUserMedia;

navigator.getUserMedia({
video: true
}, function (stream) {
_this.video = document.getElementById("video")
_this.video.srcObject = stream;
_this.video.play();   

/*document.getElementById("snap").addEventListener('click', function() {
ctx.drawImage(video, 0, 0, width, height);
var url = canvas.toDataURL('image/png');
document.getElementById('download').href = url;
});*/
}, function (error) {
alert(error.name)
console.log(error.name || error);
});
},

screenShot() {
this.video = document.getElementById("video")
var w = this.video.width,
h = this.video.height,
scale = w / h;
w = 240 || w;  
h = w / scale;
var canvas = document.createElement('canvas');
canvas.width = w;
canvas.height = h;
var ctx = canvas.getContext('2d');
ctx.drawImage(this.video, 0, 0, w, h);

var base64 = canvas.toDataURL('image/*', 1 || 0.8 );
this.face_img = base64
},

start_in() {
if(localStorage.getItem('face_img')){
this.screenShot();
this.face_img = this.face_img.split('base64,')[1];
var match_img = localStorage.getItem('face_img');
var params = `[{
"image": "${match_img}",
"image_type": "BASE64",
"face_type": "LIVE",
"quality_control": "LOW",
"liveness_control": "LOW"
},{
"image": "${this.face_img}",
"image_type": "BASE64",
"face_type": "IDCARD",
"quality_control": "LOW",
"liveness_control": "LOW"
}]`;
this.$API.IDS_API.match(params).then(res => {
if(res.data.error_code == 0) {
this.is_face = false;
this.$router.push('/dui')
} else {
MessageBox('', ',,');
}
console.log('',res);
}).catch(err => {
MessageBox('', ',');
console.log('',err);
})
} else {
MessageBox('', ',');
}
}
}
}



